import React, { Suspense } from "react";
import {
  BrowserRouter as Router,
  Switch as RouterSwitch,
  Route
} from "react-router-dom";
import LinearProgress from "@material-ui/core/LinearProgress";

import CssBaseline from "@material-ui/core/CssBaseline";
import MainPage from "./components/MainPage";

import { ThemeProvider as MuiThemeProvider } from "@material-ui/core/styles";
import SwitchComponent from "./components/SwitchComponent";
import { AppStateContext } from "./components/AppState";

const DataTable = React.lazy(() => import("./components/DataTable"));
const ConfigMenuPage = React.lazy(() => import("./components/ConfigMenu"));
const ConfigMenuPage2 = React.lazy(() => import("./components/ConfigMenu2"));
const DataTableRestApi = React.lazy(() =>
  import("./components/DataTableRestApi")
);
const FormSteps = React.lazy(() => import("./components/FormSteps"));
const WikiUp = React.lazy(() => import("./components/WikiUp"));
const WikiUpPage = React.lazy(() => import("./components/WikiUpPage"));
const VideoBg = React.lazy(() => import("./components/VideoBg"));
const ArticleList = React.lazy(() => import("./components/ArticleList"));
const SlideShowPage = React.lazy(() => import("./components/SlideShow"));
const DateRangeCalendar = React.lazy(() => import("./components/DateRangeCal"));
const ConfigFormPage = React.lazy(() => import("./components/ConfigForm"));

function Main() {
  const store = React.useContext(AppStateContext);

  console.log(store.currentTheme.palette.type);

  const [isOpen, setIsOpen] = React.useState();

  const toggle = () => {
    setIsOpen(!isOpen);
  };

  return (
    <MuiThemeProvider theme={store.currentTheme}>
      <SwitchComponent />
      <CssBaseline />
      <Router>
        <RouterSwitch>
          <Route
            path="/bio"
            render={() => {
              return <MainPage toggle={toggle} isOpen={false} />;
            }}
          ></Route>
          <Route
            path="/"
            exact
            render={() => {
              return <MainPage toggle={toggle} isOpen={true} />;
            }}
          ></Route>
          <Suspense fallback={<LinearProgress />}>
            <Route path="/calendar" render={() => <DateRangeCalendar />} />
            <Route path="/slideshow" render={() => <SlideShowPage />} />
            <Route path="/configform" render={() => <ConfigFormPage />} />
            <Route path="/articleList" render={() => <ArticleList />} />
            <Route path="/videoapp" render={() => <VideoBg />} />
            <Route path="/manual" render={() => <WikiUpPage />} />
            <Route path="/wiki" render={() => <WikiUp />} />
            <Route path="/forms-teps" render={() => <FormSteps />} />
            <Route
              path="/datatable-rest-api"
              render={() => <DataTableRestApi />}
            />
            <Route path="/datatable" render={() => <DataTable />} />
            <Route path="/configmenu" render={() => <ConfigMenuPage />} />
            <Route path="/test" render={() => <ConfigMenuPage2 />} />
          </Suspense>
        </RouterSwitch>
      </Router>
    </MuiThemeProvider>
  );
}

export default Main;
